        and use resources more efficiently.
        <br />
        <br />
        Lagarsoft allows you to validate your ideas in weeks, not months.
      </p>
    </container>
  </container>

  <div id="blob-container">
    <img class="z-20 w-2/5 max-w-lg" alt="" id="blob-1" src="img/vector2.svg" />
    <div
