    <img class="z-20 w-1/5" alt="" id="blob-3" src="img/vector1.svg" />
    <img class="z-10 w-1/6 max-w-sm opacity-25 sm:opacity-100" alt="" id="blob-4" src="img/vector7.svg" />

    <div class="force-show" />

  </div>
</container>

